<template>
  <div>
    <div class="subject">
      <div class="import-out">
        <div class="title" ref="print">作业预览</div>
        <!-- <Subjects :questionList="questionList" id="subOutputRank-print" ></Subjects> -->
        <div class="model" v-show="Conter">
          <div class="arrangement">
            <!-- <h4 class="tit tc ft20 fb col39a">
            作业布置
            <img @click="close" src="../../../static/images/close.png" alt />
            </h4>-->
            <div class="con submit-test-dialog">
              <p style="text-align:center;color:red;" v-if="!identity">请完善您的身份信息,以便布置作业</p>
              <el-form
                :model="ruleForm2"
                status-icon
                :rules="rules2"
                ref="ruleForm2"
                label-width="80px"
                class="demo-ruleForm"
                :inline="true"
              >
                <el-form-item label="作业名称" prop="account" class="zh_icon" style="width:50%">
                  <el-input
                    v-model.number="ruleForm2.account"
                    placeholder="请输入作业名称"
                    @input="e => ruleForm2.account = validForbid(e)"
                    style="width:183px"
                  ></el-input>
                </el-form-item>
                <el-form-item label="选择班级" prop="classIdList" class="yzm">
                  <el-select
                    v-model="ruleForm2.classIdList"
                    placeholder="请选择班级"
                    multiple
                    @change="changeClass"
                  >
                    <el-option
                      v-for="(item, i) in createClass"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                  <!-- <el-button
                    style="background-color:#f25c29;color:#fff;border:none"
                    @click="createClassed"
                  >创建班级</el-button>-->
                </el-form-item>
                <el-form-item label="作业类型" prop="type" class="bj" style="width:50%">
                  <el-select v-model="workType.label" placeholder="请选择作业类型">
                    <el-option
                      v-for="item in workType"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item label="专题" prop="date1" class="zh_icon">
                  <el-select v-model="special.value" clearable placeholder="请选择">
                    <el-option
                      v-for="item in special"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>

                  <!-- <el-form-item prop="date1"> -->
                  <!-- <div class="block">
                    <el-date-picker v-model="ruleForm2.date1" type="datetime" placeholder="选择日期时间"></el-date-picker>
                  </div>-->
                  <!-- </el-form-item> -->
                </el-form-item>
                <el-form-item label="展现分类" prop="type" class="bj" style="width:50%">
                  <el-select v-model="ruleForm2.type" placeholder="请选择作业类型" @change="chooseWork">
                    <el-option label="结构化作业" value="1"></el-option>
                    <el-option label="非结构化作业(扩散性或探究型作业)" value="2"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="布置情况" prop="question">
                  <el-select v-model="workKnow.label" placeholder="请选择作业类型" @change="chooseWork">
                    <el-option
                      v-for="item in workKnow"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="知识水平" prop="horizontal" class="bj" style="width:50%">
                  <el-select v-model="ruleForm2.horizontal" placeholder="请选择知识水平">
                    <el-option label="初级" value="1"></el-option>
                    <el-option label="中级" value="2"></el-option>
                    <el-option label="高级" value="3"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="预计用时" prop="number" class="zh_icon">
                  <el-input
                    v-model.number="ruleForm2.number"
                    @input="e=>ruleForm2.number=validNumber(e)"
                    placeholder="请输入用时"
                  >
                    <template slot="append">分钟</template>
                  </el-input>
                </el-form-item>
                <!-- <el-form-item class="dt">
                <el-button type="primary" @click="questions('ruleForm2','0')">确定</el-button>
                <el-button type="primary" @click="questions('ruleForm2','1')">取消</el-button>
                </el-form-item>-->
              </el-form>
            </div>
          </div>
        </div>
        <Subjects :questionList="questionList"></Subjects>
        <div class="nodata" v-if="false">
          <img src="../../../../static/images/nodata.png" alt />
          <span>暂无导入试题</span>
        </div>
      </div>
    </div>
    <div class="confirm" id="printMe">
      <!-- <el-button type="warning" class="myBtn1" @click="release" v-if="contentName">发布</el-button> -->
      <el-button
        type="warning"
        class="myBtn1"
        @click="questions('ruleForm2','0')"
        v-if="contentName"
      >发布</el-button>
      <el-button type="primary" class="myBtn" @click="againEdit">重新编辑</el-button>
      <el-button type="primary" class="myBtn" @click="printContent">打印</el-button>
      <!-- <el-button type="primary" class="myBtn" @click="toFabu">取消</el-button> -->
    </div>
    <!-- 隐藏弹框 -->
    <!-- <div class="layer" :style="show?'display:block':''">
      <div class="model">
        <div class="arrangement">
          <h4 class="tit tc ft20 fb col39a">
            作业布置
            <img @click="close" src="../../../static/images/close.png" alt />
          </h4>
          <div class="con submit-test-dialog">
            <p style="text-align:center;color:red;" v-if="!identity">请完善您的身份信息,以便布置作业</p>
            <el-form
              :model="ruleForm2"
              status-icon
              :rules="rules2"
              ref="ruleForm2"
              label-width="80px"
              class="demo-ruleForm"
            >
              <el-form-item label="作业类型" prop="type" class="bj">
                <el-select v-model="ruleForm2.type" placeholder="请选择作业类型" @change="chooseWork">
                  <el-option label="结构化作业" value="1"></el-option>
                  <el-option label="非结构化作业(扩散性或探究型作业)" value="2"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="选择班级" prop="classIdList" class="yzm">
                <el-select
                  v-model="ruleForm2.classIdList"
                  placeholder="请选择班级"
                  multiple
                  @change="changeClass"
                >
                  <el-option
                    v-for="(item, i) in createClass"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
                <el-button type="primary" @click="createClassed">创建班级</el-button>
              </el-form-item>
              <el-form-item label="作业名称" prop="account" class="zh_icon">
                <el-input
                  v-model.number="ruleForm2.account"
                  placeholder="请输入作业名称"
                  @input="e => ruleForm2.account = validForbid(e)"
                ></el-input>
              </el-form-item>
              <el-form-item label="截止时间" prop="date1" class="zh_icon">
                
                <div class="block">
                  <el-date-picker v-model="ruleForm2.date1" type="datetime" placeholder="选择日期时间"></el-date-picker>
                </div>
              </el-form-item>
              <el-form-item label="知识点" prop="desc">
                <el-input type="textarea" v-model="ruleForm2.desc" placeholder="知识点"></el-input>
              </el-form-item>
              <el-form-item label="解决问题" prop="question">
                <el-input type="textarea" v-model="ruleForm2.question" placeholder="解决问题"></el-input>
              </el-form-item>
              <el-form-item label="知识水平" prop="horizontal" class="bj">
                <el-select v-model="ruleForm2.horizontal" placeholder="请选择知识水平">
                  <el-option label="初级" value="1"></el-option>
                  <el-option label="中级" value="2"></el-option>
                  <el-option label="高级" value="3"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="预计用时" prop="number" class="zh_icon">
                <el-input
                  v-model.number="ruleForm2.number"
                  @input="e=>ruleForm2.number=validNumber(e)"
                  placeholder="请输入用时"
                >
                  <template slot="append">分钟</template>
                </el-input>
              </el-form-item>
              <el-form-item class="dt">
                <el-button type="primary" @click="questions('ruleForm2','0')">确定</el-button>
                <el-button type="primary" @click="questions('ruleForm2','1')">取消</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </div>
    </div>-->
    <!-- 创建班级-->
    <div class="creatClass" v-show="centerDialogVisible">
      <creatClass
        :centerDialogVisible="centerDialogVisible"
        @callOf="callOfa"
        @closeDialog="closeDialog"
        @submitForm="submitFormq"
      ></creatClass>
    </div>
  </div>
</template>
<script>
import Subjects from "@/components/subject/subject";
import creatClass from "@/components/creatClass/creatClass";

export default {
  components: {
    Subjects,
    creatClass
  },
  data() {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入预计用时"));
      } else if (!Number.isInteger(Number(value))) {
        //   console.log(value)
        callback(new Error("请输入数字值"));
      } else {
        callback();
      }
    };
    return {
      SubjectId: JSON.parse(sessionStorage.getItem("subjectId")),
      Conter: true,
      centerDialogVisible: false,
      answerindex: 0,
      workKnow: [
        { label: "已布置", value: "1" },
        { label: "未布置", value: "2" }
      ],
      workType: [
        { label: "巩固作业", value: "1" },
        { label: "测试作业", value: "2" }
      ],
      special: [
        { label: "基础型", value: "1" },
        { label: "专题型", value: "2" },
        { label: "竞赛型", value: "3" }
      ],
      difficulty: [
        { name: "解释性" },
        { name: "应用性" },
        { name: "拓展性" },
        { name: "自定义" }
      ],
      difficultyindex: "",
      time: [
        { name: "宽松" },
        { name: "合适" },
        { name: "超时" },
        { name: "自定义" }
      ],
      timeindex: "",
      target: [
        { name: "但一知识目标" },
        { name: "多知识目标" },
        { name: "自定义" }
      ],
      homeworkId: "",
      types: [],
      questionList: [],
      ruleForm2: {
        type: "2",
        classIdList: [],
        account: "",
        horizontal: "",
        date1: "",
        desc: "",
        question: "",
        number: 30
      },

      rules2: {
        type: [
          { required: true, message: "请选择作业类型", trigger: "change" }
        ],
        classIdList: [
          { required: true, message: "请选择班级", trigger: "change" }
        ],
        account: [
          { required: true, message: "请输入作业名称", trigger: "blur" }
        ],
        date1: [
          {
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
        desc: [{ required: false, message: "请填写知识点", trigger: "blur" }],
        question: [
          { required: false, message: "请填写解决问题", trigger: "blur" }
        ],
        horizontal: [
          { required: true, message: "请选择只是水平", trigger: "blur" }
        ],
        number: [{ required: true, validator: checkAge, trigger: "blur" }]
      },
      identity: sessionStorage.getItem("identity"),
      createClass: [],
      show: false,
      contentName: true
    };
  },
  created() {
    let currentDate = new Date();

    // if (this.$route.query.contentName) {
    //   this.contentName = this.$route.query.contentName;
    //   this.Conter = true
    // }
    //   console.log(this.$route.query)
    // if(this.$route.query.contentName==""){
    //    this.contentName =true
    //    this.Conter = true

    // }
    if (this.$route.query.contentName == undefined) {
      this.contentName = false;
      this.Conter = false;
    }
    this.homeworkId = this.$route.query.homeworkId;
    this.ruleForm2.account = this.$route.query.contentName;
    this.ruleForm2.date1 = `${currentDate.getFullYear()}-${this.addZore(
      Number(currentDate.getMonth()) + 1
    )}-${this.addZore(currentDate.getDate())} 23:59:59`;
    this.querySujectList();
    // this.ruleForm2.horizontal = String(this.$route.query.subjectId);

    this.QuestionType();
    this.queryCreateClass();
    this.queryHomeworkById();
  },
  mounted() {
    //console.log(this.ruleForm2)
  },
  methods: {
    //打印

    printContent(e) {
      let subOutputRankPrint = document.getElementById("subOutputRank-print");
      //   console.log(subOutputRankPrint.innerHTML);
      let newContent = subOutputRankPrint.innerHTML;
      let oldContent = document.body.innerHTML;
      document.body.innerHTML = newContent;
      window.print();
      window.location.reload();
      document.body.innerHTML = oldContent;

      return false;
    },
    addZore(e) {
      return e < 10 ? "0" + e : e;
    },
    // 获取知识水平
    querySujectList() {
      this.$post("/likework/querySujectList.do").then(res => {
        if (res.code == "5000") {
          for (var key in res.data) {
            res.data[key].forEach((item, index) => {
              if (item.id == this.$route.query.subjectId) {
                this.ruleForm2.horizontal =
                  key == "小学" ? "1" : key == "初中" ? "2" : "3";
              }
            });
          }
        }
      });
    },
    // 选择班级
    changeClass(e) {
      this.ruleForm2.classIdList = e;
    },
    // 选择作业类型
    chooseWork(e) {
      this.ruleForm2.type = e;
    },
    close() {
      this.show = false;
    },
    againEdit() {
      this.$router.push({
        path: "/homeCommon/workCenter",
        query: { homeworkId: this.$route.query.homeworkId }
      });
    },
    //创建班级
    submitFormq(data) {
      // console.log(data);
      this.centerDialogVisible = data;
      this.queryCreateClass();
    },
    //创建班级关闭弹框
    closeDialog(val) {
      this.centerDialogVisible = val;
    },
    //创建班级取消
    callOfa(val) {
      this.centerDialogVisible = val;
    },
    //创建班级弹框
    createClassed() {
      this.centerDialogVisible = true;
    },
    questions(form, e) {
      //  console.log(e==1)
      if (e == 1) {
        this.show = false;
        return false;
      }
      // console.log(this.ruleForm2);
      let date = new Date(this.ruleForm2.date1);
      let data = {
        id: this.homeworkId,
        classIdList: this.ruleForm2.classIdList.join(":"),
        subjectId: this.$route.query.subjectId,
        textbookId: this.$route.query.textbookId,
        chapterId: this.$route.query.chapterId,
        barId: this.$route.query.barId,
        contentId: this.$route.query.contentId,
        homeworkName: this.ruleForm2.account,
        solveProblem: this.ruleForm2.question,
        knowledge: this.ruleForm2.desc,
        endTime: `${date.getFullYear()}-${this.addZore(
          date.getMonth() + 1
        )}-${this.addZore(date.getDate())} ${this.addZore(
          date.getHours()
        )}:${this.addZore(date.getMinutes())}:${this.addZore(
          date.getSeconds()
        )}`,
        expectTime: this.ruleForm2.number,
        level: this.ruleForm2.horizontal,
        type: this.ruleForm2.type
      };
      // console.log(data)
      this.$refs["ruleForm2"].validate(valid => {
        if (valid) {
          this.$post("/likework/updateClassHomework.do", data).then(res => {
            // console.log(res);
            if (res.code == "5000") {
              this.publishHomeworkById(res.data);

              //   setTimeout(() => {
              //     this.$router.push({
              //       path: "/homeCommon/questions",
              //       query:{homeworkId:res.data,title:`${this.type},${this.subjectName},${this.book},${this.grade},${this.chapter},${this.section},${this.content}`,subjectId:this.subjectId,tabsNum:tabsNum,update:1}
              //     });
              //   }, 1500);
            }
          });
        } else {
          return false;
        }
      });
    },
    //获取创建的班级列表
    queryCreateClass() {
      this.$post("/likework/queryCreateClass.do").then(response => {
        //console.log(response)
        if (response.code == "5000") {
          this.createClass = response.data;
          this.classId = response.data[0].id;
          //    console.log(response.data[0].id, "99");
          //this.queryClassHomeworkDateRecord();//根据班级和作业查询班级数据报表
        }
      });
    },
    release() {
      this.show = !this.show;
    },
    // 根据id发布作业接口
    publishHomeworkById(e) {
      let data = {
        homeworkId: e
      };
      this.$post("/likework/publishHomeworkById.do", data).then(res => {
        if (res.code == "5000") {
          this.$message({
            showClose: true,
            message: "发布作业成功!",
            type: "success"
          });
          this.$router.push({
            path: "/homeCommon/workCenter/unworkable",
            query: {
              flag: "1"
            }
          });
        }
      });
    },
    // 获取题目全部题型接口
    QuestionType(e) {
      let data = { subjectId: this.subjectId };
      this.$post("/likework/queryQuestionTypeSelect.do", data).then(res => {
        if (res.code == "5000") {
          this.types = res.data;
        }
      });
    },
    nandu(e, callback) {
      this.difficulty.forEach((item, index) => {
        if (item.name == e) {
          callback(index);
        }
      });
    },
    shijian(e, callback) {
      this.time.forEach((item, index) => {
        if (item.name == e) {
          callback(index);
        }
      });
    },
    mubiao(e, callback) {
      this.target.forEach((item, index) => {
        if (item.name == e) {
          callback(index);
        }
      });
    },
    leixing(e, callback) {
      this.types.forEach((item, index) => {
        if (item.id == e) {
          callback(index);
        }
      });
    },
    // 根据id查询作业详情接口
    queryHomeworkById(e) {
      let data = { homeworkId: this.homeworkId };
      let that = this;
      this.$post("/likework/queryHomeworkById.do", data).then(res => {
        if (res.code == "5000") {
          this.solve = res.data.solveProblem;
          this.homeworkName = res.data.homeworkName;
          // this.value = res.data.classList[0].name;
          let classId = [],
            name = [];
          res.data.classList.forEach((item, index) => {
            name.push(item.name);
            classId.push(item.id);
          });
          this.value = classId;
          // this.classId = classId.join(':');
          // this.type = res.data.type==1?'结构化作业':'非结构化作业';
          this.endTime = res.data.endTime;
          this.knowledge = res.data.knowledge;
          this.solveProblem = res.data.solveProblem;
          this.expectTime = res.data.expectTime;
          // this.level = res.data.level==1?'初级':res.data.level==2?'中级':'高级';
          this.type = String(res.data.type);
          this.level = String(res.data.level);
          res.data.questionList.forEach((item, index) => {
            item.t = null;
            item.recorder = null;
            item.value = "";
            item.checked = 1;
            if (item.property === null) {
              (item.difficultyindex = 0),
                (item.timeindex = 0),
                (item.targetindex = 0),
                that.leixing(item.type, res => {
                  item.QuestionTypeindex = res;
                });
              return false;
            }
            let property = JSON.parse(item.property);
            that.nandu(property["难度"], response => {
              //    console.log(response);
              item.difficultyindex = response;
            });
            that.shijian(property["时间"], response => {
              item.timeindex = response;
            });
            that.mubiao(property["目标"], response => {
              item.targetindex = response;
            });
            that.leixing(item.type, res => {
              item.QuestionTypeindex = res;
            });
            //   console.log(item);
          });

          this.questionList = res.data.questionList;
          this.queryCreateClass();
          // this.question = res.data.code;
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.subject {
  background: #fff;
  padding-bottom: 20px;
  .import-out {
    width: 80%;
    margin: 0 auto;
    .title {
      // text-align: center;
      margin-bottom: 10px;
      font-size: 18px;
      color: #333;
      padding-top: 20px;
    }
  }
}
.confirm {
  background: #fff;
  padding-bottom: 20px;
  text-align: center;
  .myBtn {
    background-color: #159484;
    border: none;
  }
  .myBtn1 {
    background-color: #f25c29;
    border: none;
  }
}
.layer {
  .model {
    width: 700px;
    .arrangement {
      width: 100%;
      background: #ffffff;
      margin: 0 20px;
      padding: 0 20px;
      box-sizing: border-box;
      .yzm {
        .el-select {
          width: 80%;
        }
        .el-button {
          right: 0;
        }
      }
      .el-select {
        width: 100%;
      }
      .tit {
        position: relative;
        img {
          cursor: pointer;
          position: absolute;
          right: 0;
          top: 0;
          width: 25px;
          height: 25px;
        }
      }
      .details {
        margin: 20px;
      }
      .con {
        margin: 20px 0 0 0;
      }
    }
  }
}
</style>


